@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@600&display=swap);
.menu {
    position: absolute;
    height: 100vh;
    width: 250px;
    background-color: white;
    left: 0;
    top: 0;
    right: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 2;
    background-color: #222223;
    visibility: hidden;
    opacity: 0;
}

.menu ul {
    margin-top: 0px;
    padding: 0px;
    color: black;
    list-style-type: none;
    text-align: center;
}

.menu ul li:last-child {
    border-bottom: none;
}

.menu ul li a {
    padding: 15px 20px;
    font-size: 20px;
    text-decoration: none;
    display: block;
    color: #a1a1a3;
}
.menu ul li a:hover,
.menu ul li a:active,
.menu ul li a:visited,
.menu ul li a:link {
    text-decoration: none;
    color: #696969;
}

.spinner {
    width: 30px;
    height: 30px;
}

.spinner svg {
    width: 100%;
    height: 100%;
    overflow: visible;
    -webkit-animation: rotation 1455ms infinite linear;
            animation: rotation 1455ms infinite linear;
}

.spinner circle {
    stroke: currentColor;
    stroke-dasharray: 44px;
    stroke-dashoffset: 11px;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotatey(180deg) rotate(90deg);
            transform: rotatey(180deg) rotate(90deg);
    -webkit-animation: spinner 3850ms infinite ease;
            animation: spinner 3850ms infinite ease;
}

@-webkit-keyframes spinner {
    0% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(0deg) rotate(0deg);
                transform: rotatey(0deg) rotate(0deg);
    }
    25% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(0deg) rotate(0deg);
                transform: rotatey(0deg) rotate(0deg);
    }
    25.0001% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(180deg) rotate(270deg);
                transform: rotatey(180deg) rotate(270deg);
    }
    50% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(180deg) rotate(270deg);
                transform: rotatey(180deg) rotate(270deg);
    }
    50.0001% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(0deg) rotate(180deg);
                transform: rotatey(0deg) rotate(180deg);
    }
    75% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(0deg) rotate(180deg);
                transform: rotatey(0deg) rotate(180deg);
    }
    75.0001% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(180deg) rotate(90deg);
                transform: rotatey(180deg) rotate(90deg);
    }
    100% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(180deg) rotate(90deg);
                transform: rotatey(180deg) rotate(90deg);
    }
}

@keyframes spinner {
    0% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(0deg) rotate(0deg);
                transform: rotatey(0deg) rotate(0deg);
    }
    25% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(0deg) rotate(0deg);
                transform: rotatey(0deg) rotate(0deg);
    }
    25.0001% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(180deg) rotate(270deg);
                transform: rotatey(180deg) rotate(270deg);
    }
    50% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(180deg) rotate(270deg);
                transform: rotatey(180deg) rotate(270deg);
    }
    50.0001% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(0deg) rotate(180deg);
                transform: rotatey(0deg) rotate(180deg);
    }
    75% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(0deg) rotate(180deg);
                transform: rotatey(0deg) rotate(180deg);
    }
    75.0001% {
        stroke-dashoffset: 11px;
        -webkit-transform: rotatey(180deg) rotate(90deg);
                transform: rotatey(180deg) rotate(90deg);
    }
    100% {
        stroke-dashoffset: 33px;
        -webkit-transform: rotatey(180deg) rotate(90deg);
                transform: rotatey(180deg) rotate(90deg);
    }
}

@-webkit-keyframes rotation {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotation {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.wrapper-spinner {
    position: absolute;
    left: 0;
    right: 0;
    margin: 10px auto 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: rgb(56, 56, 56);
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Comfortaa', Arial, Helvetica, sans-serif;
}

.app {
    text-align: center;
    min-height: 100vh;
    background: #fff;
    padding: 0px 20px;
}

.app.search .wrapper-search {
    visibility: visible;
    opacity: 1;
}

.app.show-menu .menu {
    visibility: visible;
    opacity: 1;
}

.app.search header h1, .app.show-menu header h1{
    color: #a1a1a3;
}

.app.search header .hamburguer span, .app.show-menu header .hamburguer span{
    background: #a1a1a3;
}

.app.search header .icon-search svg, .app.show-menu header .icon-search svg {
    fill: #a1a1a3;
}

.error {
    display: inline-block;
    text-align: center;
    margin: 30px;
    background: rgb(221, 85, 70);
    color: rgb(247, 232, 230);
    padding: 20px 30px;
    border-radius: 5px;
    text-decoration: none;
    transition: 0.3s ease all;
}

.error:hover {
    background: rgb(235, 95, 79);
    box-shadow: 3px 3px 0 rgb(141, 38, 26);
}

header {
    padding: 0 20px;
    border-bottom: 1px #cdcdcd solid;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    max-width: 1170px;
    margin: 0 auto;
    position: relative;
    z-index: 3;
}

h1 {
    margin-top: 0;
    margin-bottom: 0px;
    color: rgb(56, 56, 56);
    font-size: 20px;
    text-transform: uppercase;
}

.wrapper-search {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #222223;
    padding-top: 120px;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: all 200ms ease-in-out;
}

form {
    margin-bottom: 50px;
}

input[type='text'] {
    min-width: 400px;
    padding: 15px 20px;
    border: none;
    border-bottom: 1px rgba(255, 255, 255, 0.2) solid;
    outline: none;
    margin-right: 12px;
    font-size: 56px;
    background-color: transparent;
    color: #fff;
}

input[type='text']:active,
input[type='text']:focus {
    border-color: #999;
}

button {
    padding: 15px 20px;
    border-radius: 50px;
    border: none;
    background: #222;
    color: #bbb;
    font-size: 18px;
    cursor: pointer;
    transition: 0.3s ease all;
}

button:hover {
    background: #111;
    color: #eee;
}

.wrapper-image {
    padding: 30px 0;
    max-width: 1170px;
    margin: 0 auto;
}

.image-grid {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows: minmax(50px, 200px);
}

.image {
    background: #fff;
    border: 3px solid #fff;
    border-radius: 3px;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
        0 2px 4px -1px rgba(0, 0, 0, 0.06);
    transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) -webkit-transform;
    transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) transform;
    transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) transform, 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) -webkit-transform;
    overflow: hidden;
}

.image:hover {
    /* transform: translateY(-2px); */
}

.image img {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 300ms ease-in-out;
}

.image:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.image:nth-child(3n) {
    grid-column-end: span 2;
}

@media screen and (max-width: 768px) {
    .app {
        padding: 40px 20px;
    }
}

@media screen and (max-width: 680px) {
    .image-grid {
        grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
        grid-auto-rows: minmax(50px, 160px);
    }
}

.icon-search {
    line-height: 0px;
    cursor: pointer;
}

.icon-search svg {
    width: 30px;
    height: 30px;
    fill: rgb(56, 56, 56);
}
.icon-search svg {
    width: 30px;
    height: 30px;
}

.hamburguer {
    width: 22px;
    height: 17px;
    position: relative;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: 0.5s ease-in-out;
    cursor: pointer;
}

.hamburguer span {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background: rgb(56, 56, 56);
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: 0.25s ease-in-out;
}

.hamburguer span:nth-child(1) {
    top: 0px;
}

.hamburguer span:nth-child(2),
.hamburguer span:nth-child(3) {
    top: 7px;
}

.hamburguer span:nth-child(4) {
    top: 14px;
}

.hamburguer.open span:nth-child(1) {
    top: 18px;
    width: 0%;
    left: 50%;
}

.hamburguer.open span:nth-child(2) {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.hamburguer.open span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.hamburguer.open span:nth-child(4) {
    top: 18px;
    width: 0%;
    left: 50%;
}

